@use "../../variables.scss";

$bus-color : #032960;

transit-display {
    display: inline-flex;
    height: 1.2em;
    align-items: center;
    background-color: $bus-color;
    color: white;
    padding: 0.1em 0.3em;
    border-radius: variables.$timeline-border-radius;

}

.transit-display {
    &__route {
        white-space: nowrap;

        &--long {
            font-size: 0.7em;
        }
    }
}